.footer {
    background-color:rgb(50, 50, 50);
    border-top: 1px solid rgba(0,0,0,0.1);
}

.footer__container {
    padding: 2rem 0 6rem;
}

.footer__title,.footer__link {
    color:rgb(255, 255, 255);
}

.footer__title {
    text-align: center;
    margin-bottom: var(--mb-2);
}

.footer__link:hover {
    color:rgb(255, 255, 255);
}

.footer__list{
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-2);
}

.footer__social{
    display: flex;
    justify-content: center;
    column-gap: 1.25rem;
}

.footer__social-icon {
    font-size: 2.5rem;
}

.footer__social-link {
    background-color: var(---title-color);
    color: var(--container-color);
    font-size: 1.125rem;
    padding: 0.4rem;
    border-radius: 0.5rem;
    display: inline-flex;
}

.footer__social-link:hover{
    color:rgb(255, 255, 255);
}

.footer__copy{
    display: block;
    margin-top: 4.5rem;
    color:rgb(184, 184, 184);;
    text-align: center;
    font-size: var(--smaller-font-size);
}

.boldEvent{
    color:rgb(255, 255, 255) 
}